* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  width: 520px;
  margin-top: 16px;
  margin-bottom: 16px;
  border-color: #1f09c1;
  border-radius: 4px;
  border-width: 5px;
}

.country-list {
  display: flex;
  align-items: center;
}

.country-name {
  margin-left: 15px;
}

.country {
  display: flex;
  align-items: center;
}
